#navbar {
    background-color: maroon;
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    padding-right: 200px;
    z-index: 2;
  
  }
  
  
  #navbar > ul {
    margin: 0;
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    justify-content: space-around;
    align-items: center;
  }
  
  .navlink:hover {
    cursor: pointer;
    background-color: rgb(194, 10, 10);
  }

  .navlink {
    padding: 5px;
    font-size: 1.8rem;
    grid-column: span 1;
    display: inline-block;
  }

  .navlink {
    color: rgba(219, 218, 218, 0.87)
  }

  span {
    transition: font-size 0.5s ease-in-out;  
  }

  .bignumb {
    font-size: 2.3rem; 
  }

  #active {
    color: white;
  }

  .homelink {
    color: rgb(248, 122, 143);
    font-weight: 900;   
    font-size: 1.8rem;
  }

  #activehome {
    color: rgb(255, 186, 232);
  }

  @media (width < 1200px) {

    ul {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  @media (width < 800px) {

    ul {
      padding: 10px 10px 0 0;
      grid-template-columns: 1fr 1fr 1fr;
    }

    li {
      font-size: 1.2rem;
    }

    .homelink {
      font-size: 1.3rem;
      padding: 0px;
    }
  }

  @media (width < 500px) {

    li {
      font-size: 1rem;
    }
    .homelink {
      font-size: 1rem;
      padding: 0px;
    }
    a {
      margin: 0px; 
    }

  }
